<template>
  <section class="why_section layout_padding">
    <div class="container">
      <div class="heading_container heading_center">
        <h2>
          
        </h2>
    </div>
    <br><br>
    
      <div class="row">
          <div class="col-lg-8 offset-lg-2">
            <div class="full">
                <form @submit.prevent="submit">
                  <fieldset>
                      <input 
                      ref="nameInput" 
                      type="text" 
                      placeholder="" 
                      name="name" 
                      required/>
                      <input 
                      ref="emailInput"
                      type="email" 
                      placeholder="" 
                      name="email" 
                      required />
                      <input 
                      ref="subjectInput"
                      type="text" 
                      placeholder="" 
                      name="subject" 
                      required />
                      <textarea 
                      ref="messageInput"
                      placeholder="" 
                      required></textarea>

                      <input
                      type="submit" 
                      value=""/>
                  </fieldset>
                </form>
            </div>
          </div>
      </div>
    </div>
</section>  
</template>

<script>
import { inject, nextTick, ref } from 'vue';



export default {
  setup(){
    const nameInput = ref(null);
    const emailInput = ref(null);
    const subjectInput = ref(null);
    const messageInput = ref(null);

    const eventBus = inject('eventBus');

    const submit = ()=>{
      nextTick(()=>{
        //input
        eventBus.emit('ShowMessageDisplay',true);
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', '');
          eventBus.emit('ShowMessageButton')
        })        
        console.log(
          `: ${nameInput.value.value}`,
          `: ${emailInput.value.value}`,
          `: ${subjectInput.value.value}`,
          `: ${messageInput.value.value}`
        );
        nameInput.value.value = '';
        emailInput.value.value = '';
        subjectInput.value.value = '';
        messageInput.value.value = '';
      })
    }

    return{
      nameInput,
      emailInput,
      subjectInput,
      messageInput,
      submit
    }
  }
}
</script>

<style scoped>
h2{
  font-family: Montserrat, sans-serif;
}

</style>