<template>
  <div class="billDiv paddingLeftRight16 marginBottom12px">
    <div class="billButton"
    @click="selectReceiver('me')">
      <div       
      :class="selectedReceiver === 'me' ? 'checkCircleIt' : 'checkCircle'"></div>
      
    </div>
    <div class="billButton"
    @click="selectReceiver('other')">
      <div 
      :class="selectedReceiver === 'other' ? 'checkCircleIt' : 'checkCircle'"></div>
      
    </div>
  </div>
  <div 
  v-if="selectedReceiver === 'me'"
  class="paddingLeftRight16">
    <label></label>
    <div class="myselfDiv">
      <p> {{  }}</p>
      <p> {{  }}</p>
      <p> {{}}</p>
    </div>
  </div>
  <div v-if="selectedReceiver === 'other'">
    <div class="paddingLeftRight16 marginBottom12px">
      <label class="ReceiverName"></label>
      <input class="border6px" ref="ReceiverNameInput" type="text" placeholder="">
      <div class="pointRed" ref="errorReceiverNameMsg"></div>
    </div>

    <div class="paddingLeftRight16 marginBottom12px">
      <label class="ReceivePhone"></label>
      <input class="border6px" ref="ReceiverPhoneInput" type="text" placeholder="">
      <div class="pointRed" ref="errorReceiverPhoneMsg"></div>
      <span class="pointGreen">*   "+" +886987654321</span>
    </div>
    
    <div class="paddingLeftRight16 marginBottom12px">
      <label class="ReceiveAddress"></label>
      <input class="border6px" ref="ReceiverAdressInput" type="text" placeholder="">
      <div class="pointRed" ref="errorReceiverAAdressMsg"></div>
    </div>
  </div>
  
</template>

<script>
import { inject, nextTick, onMounted, onUnmounted, ref, watch } from 'vue';

export default {
  setup(){
    const eventBus = inject('eventBus');

    //
    const selectedReceiver = ref('me');
    const selectReceiver = (method) =>{
      selectedReceiver.value = method;
      eventBus.emit('selectedReceiverChanged',selectedReceiver.value)
    }

    //
    const  = ref('');
    const  = ref('');
    const  = ref('');

    onMounted(() => {
    eventBus.on('updateCustomerName', (name) => {
      .value = name;
    });

    eventBus.on('updateCustomerPhone', (phone) => {
      .value = phone;
    });

    eventBus.on('updateCustomerAddress', (address) => {
      .value = address;
    });
  });

    //
    const ReceiverNameInput = ref(null);
    const errorReceiverNameMsg = ref(null);

    const validateReceiverName = () =>{
      const AdressValue = ReceiverNameInput.value.value.trim();

      if(AdressValue === ''){
        errorReceiverNameMsg.value.style.display = 'block';
        ReceiverNameInput.value.classList.add('error');
        return false;
      }else{
        errorReceiverNameMsg.value.style.display = 'none';
        ReceiverNameInput.value.classList.remove('error');
        return true;
      }
    }

    //
    const ReceiverPhoneInput = ref(null);
    const errorReceiverPhoneMsg = ref(null);

    const validateReceiverPhone = ()=>{
      const ReceiverPhoneValue = ReceiverPhoneInput.value.value.trim();
      const ReceiverPhonePattern = /^09\d{1}\d{7}$|^0\d{1,2}\d{6,8}$/;

      if(ReceiverPhoneValue ==='' || !ReceiverPhonePattern.test(ReceiverPhoneValue)){
        errorReceiverPhoneMsg.value.style.display = 'block';
        ReceiverPhoneInput.value.classList.add('error');
        return false;
      }else{
        errorReceiverPhoneMsg.value.style.display = 'none';
        ReceiverPhoneInput.value.classList.remove('error');
        return true;
      }
    }

    //
    const ReceiverAdressInput = ref(null);
    const errorReceiverAAdressMsg = ref(null);

    const validateReceiverAdress = () =>{
      const AdressValue = ReceiverAdressInput.value.value.trim();

      if(AdressValue === ''){
        errorReceiverAAdressMsg.value.style.display = 'block';
        ReceiverAdressInput.value.classList.add('error');
        return false;
      }else{
        errorReceiverAAdressMsg.value.style.display = 'none';
        ReceiverAdressInput.value.classList.remove('error');
        return true;
      }
    }

    watch(selectedReceiver, async(newVal) =>{
      if(newVal === 'other'){
        await nextTick();
        if(ReceiverNameInput.value){
          ReceiverNameInput.value.addEventListener('blur', validateReceiverName)
        }
        if(ReceiverPhoneInput.value){
          ReceiverPhoneInput.value.addEventListener('blur', validateReceiverPhone)
        }
        if(ReceiverAdressInput.value){
          ReceiverAdressInput.value.addEventListener('blur', validateReceiverAdress)          
        }
      }
    })

    //submit
    const scrollTothere =(selector)=>{
      const element = document.querySelector(selector);
      if(element){
        element.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
        });
      }
    }
    
    onMounted(()=>{
      eventBus.on('sentToAskselectReceiver', async()=>{
        if(selectedReceiver.value === 'other'){
          await nextTick();
          const isNameValid  = validateReceiverName();                   
          const isPhoneValid = validateReceiverPhone();
          const isAddressValid = validateReceiverAdress();

          eventBus.emit('sentBackReceiverName',isNameValid);
          eventBus.emit('sentBackReceiverPhone',isPhoneValid);
          eventBus.emit('sentBackReceiverAddres',isAddressValid);
        }
        if(selectedReceiver.value === 'me'){
          eventBus.emit('sentBackReceiverName', true);
          eventBus.emit('sentBackReceiverPhone', true);
          eventBus.emit('sentBackReceiverAddres', true);
        }
      })
      eventBus.on('ScrollWindow',scrollTothere);
    })
    onUnmounted(()=>{
      eventBus.off('sentToAskselectReceiver')
    })

    return{
      selectedReceiver,
      selectReceiver,
      ReceiverNameInput,
      errorReceiverPhoneMsg,
      ReceiverPhoneInput,
      errorReceiverNameMsg,
      ReceiverAdressInput,
      errorReceiverAAdressMsg,
      ,
      ,
      ,
    }
  }

}
</script>

<style lang="scss" scoped>
</style>