<template>
  <section class="client_section layout_padding">
  <div class="container">
    <div class="heading_container heading_center">
        <h2>
          
        </h2>
    </div>
    <div id="carouselExample3Controls" class="carousel slide" data-ride="carousel">
        <div class="carousel-inner">
          <div class="carousel-item active">
            <div class="box col-lg-10 mx-auto" 
            v-for="(customer, index) in customerSlice" :key="index">
              <div class="img_container">
                <div class="img-box">
                  <div class="img_box-inner">
                      <img :src="customer.imageURL" alt="">
                  </div>
                </div>
              </div>
              <div class="detail-box">
                <h5>
                  {{ customer.name }}
                </h5>
                <h6>
                  
                </h6>
                <p>
                  {{ customer.say }}
                </p>
              </div>
            </div>
          </div>
          
          <div class="carousel-item" 
          v-for="(customer, index) in customerSliceForOther" :key="index" >
              <div class="box col-lg-10 mx-auto">
                <div class="img_container">
                    <div class="img-box">
                      <div class="img_box-inner">
                          <img :src="customer.imageURL" alt="">
                      </div>
                    </div>
                </div>
                <div class="detail-box">
                    <h5>
                      {{ customer.name }}
                    </h5>
                    <h6>
                      
                    </h6>
                    <p>
                      {{ customer.say }}
                    </p>
                </div>
              </div>
          </div>

          
        </div>
        <div class="carousel_btn_box">
          <a class="carousel-control-prev" href="#carouselExample3Controls" role="button" data-slide="prev">
          <i class="fa fa-long-arrow-left" aria-hidden="true"></i>
          <span class="sr-only">Previous</span>
          </a>
          <a class="carousel-control-next" href="#carouselExample3Controls" role="button" data-slide="next">
          <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
          <span class="sr-only">Next</span>
          </a>
        </div>
    </div>
  </div>
</section>
</template>

<script>
import { computed, reactive } from 'vue';
export default {
  setup() {
    const customer = reactive([
      {
        name: '',
        imageURL: `images/person2.jpg`,
        say: ''
      },
      {
        name: '',
        imageURL: `images/person1.jpg`,
        say: ''
      },
      {
        name: '',
        imageURL: `images/person3.jpg`,
        say: ''
      },
      {
        name: '',
        imageURL: `images/person4.jpg`,
        say: ''
      },
      {
        name: '',
        imageURL: `images/person5.jpg`,
        say: ''
      },
    ]);

    const customerSlice = computed(() =>{
      return customer.slice(0,1);
    })

    const customerSliceForOther = computed(() =>{
      return customer.slice(1);
    })
    return {
      customer,
      customerSlice,
      customerSliceForOther
    };
  }

}
</script>

<style scoped>
h2{
  font-family: Montserrat, sans-serif;
}

</style>